<template>
  <div class="login">
  	<!-- <Unit></Unit> -->
  	<Download></Download>
  	<div class="login_content">
  		<img src="../../static/img/login_bg_pic@3x.png" alt="">
		<p class="login_phoneNumber">
			<input ref='moblie' type="text" placeholder="手机号" maxlength="11"  />
			<span id='send_verification' ref='send_verification_code' @click="send_verification_code">发送验证码</span>
		</p>
		<p class="login_verification">
			<input type="text" placeholder="验证码" ref='login_verification' />
		</p>
		<div id="login_btn" @click="login">
			登录
		</div>
		<h3 class="login_desc">
			<span></span>
			若您输入的手机号未注册，将会进入注册流程。注册即视为同意 <a v-bind:href='agreeUrl'>《品库用户协议》。</a>
		</h3>
  	</div>
  </div>
</template>

<script>
import Unit from './common_components/Unit'
import { Toast } from 'mint-ui';
import axios from 'axios'
export default {
  name: 'Login',
  data () {
    return {
      msg: 'suolun',
      initcount:60,
      send_state:true,
      timer:null,
      agreeUrl:''
    }
  },
  computed:{
      redirectUrl () {
        return this.$store.state.redirectUrl
      },
      userAgreementUrl(){
      	return this.$store.state.userAgreementUrl
      }
  },
  created(){
  	this.agreeUrl = this.userAgreementUrl ? this.userAgreementUrl : localStorage.userAgreementUrl;
  	// console.log(this.agreeUrl)
  },
  methods:{
  	send_verification_code(){
  		let phoneNumber = this.$refs.moblie.value;
  		if(this.checkmobile() && this.send_state){
  			let params = new FormData();
				params.append('phone',phoneNumber );
				params.append('type', 0);
  			axios.post(this.$api+'/h5/users/send_confirm_code',params).then((res)=>{
  				if(res.data.succ){
  					let instance = Toast('验证码发送成功');
					setTimeout(() => {
					  instance.close();
					}, 2000);
  					this.countDown()
  				}else{
  					let instance = Toast(res.data.errorCode.message);
					setTimeout(() => {
					  instance.close();
					}, 2000);
  				}
  				
  			}).catch(error=>{
  				let instance = Toast('验证码发送失败,请刷新或检查网络后重试');
				setTimeout(() => {
				  instance.close();
				}, 2000);
  			})
  		}

  	},
  	login(){
  		let verificationCode = this.$refs.login_verification.value,
  			phoneNumber 	 = this.$refs.moblie.value;
		if(verificationCode == ''){
			let instance = Toast('请输入验证码');
			setTimeout(() => {
			  instance.close();
			}, 2000);
			return ;
		}

  		if(this.checkmobile()){
  			// console.log(this.$store.state.redirectUrl)
  			let params = new FormData();
  				params.append('phone',phoneNumber);
  				params.append('confirmCode',verificationCode);
  			axios.post(this.$api+'/h5/users/login_by_code',params).then((res)=>{
  				if(res.data.succ){
  					this.$router.push({
			  			path:this.$store.state.redirectUrl
			  		})
  				}else{
  					let instance = Toast(res.data.errorCode.message);
					setTimeout(() => {
					  instance.close();
					}, 2000);
  				}
  				
  			}).catch(error=>{
  				let instance = Toast('网络好像有问题,请稍候再试试');
				setTimeout(() => {
				  instance.close();
				}, 2000);
  			})
  		}
  	},
  	checkmobile(){
  		//检测手机号码
  		let moblieValue = this.$refs.moblie.value
  		if( moblieValue== ''){
  			let instance = Toast('手机号码不能为空');
			setTimeout(() => {
			  instance.close();
			}, 2000);
  			return false;
  		}
  		//检测手机号码是否正确
  		let myreg=/^[1][0-9]{10}$/;
  		if (!myreg.test(moblieValue)) { 
  			let instance = Toast('手机号码有误');
			setTimeout(() => {
			  instance.close();
			}, 2000);
            return false;  
	    }
	    return true;
  	},
  	countDown(){
  		this.send_state = false;
  		this.timer = setInterval(()=>{
  			this.initcount--;
  			this.$refs.send_verification_code.innerHTML = this.initcount+'秒';
  			if(this.initcount<=0){
  				this.initcount = 60;
  				this.$refs.send_verification_code.innerHTML='发送验证码';
  				this.send_state = true;
  				clearInterval(this.timer);
  			}
  		},1000)
  	},
  },
  destroyed(){
    clearInterval(this.timer)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../common/css/var.scss';
.login{
	.login_content{
		max-width: 414px;
		margin: 0 auto;
		margin-top: 44px;
		width: 100%;
		img{
			width: 100%;
		}
		p{
			width: 335px;
			margin: 0 auto;
			box-sizing:border-box;
			height: 54px;
			font-size: 14px;
			color: #b2b2b2;
			line-height: 54px;
			border-bottom:1px solid #f4f4f4;/*no*/
		}
		p.login_phoneNumber{
			margin-top: 31px;
			display: flex;
			align-items:center;
			justify-content:center;
			input{
				flex:1;
				height: 25px;
				padding: 5px 10px;
				font-size: 14px;
			}
			span{
				font-size: 12px;
				border:1px solid #737373; /*no*/
				border-radius:12px;
				height: 24px;
				line-height: 24px;
				padding: 0 10px;
				width: 76px;
				text-align: center;
			}
		}
		p.login_verification{
			display: flex;
			align-items:center;
			input{
				flex:1;
				height: 25px;
				padding: 5px 10px;
				font-size: 14px;
			}
		}
		#login_btn{
			width: 335px;
			height: 48px;
			margin:0 auto;
			text-align: center;
			line-height: 48px;
			background: $pink;
			border-radius: 24px;
			box-shadow:0 2px 14px 0 rgba(232,13,65,0.30);
			font-size:20px;
			color:#ffffff;
			font-family:PingFangSC-Regular;
			margin-top: 77px;
		}
		.login_desc{

			width: 324px;
			margin: 31px auto;
			font-size:12px;
			color:#99a1b2;
			line-height: 20px;
			position: relative;
			a{
				color: #91B7F3;
			}
			span{
				top: 7px;
				left: -10px;
				position: absolute;
				background:#fceded;
				width:6px;
				height:6px;
				border-radius:50%;
			}
		}
	}
}
input::-webkit-input-placeholder{
	color: #b2b2b2;
}
</style>



